      // 	// imgobj.draggable=true;
      // 	// imgobj.innerHTML = 'dfsfsdf'
      // 	document.getElementById("target").appendChild(imgobj)
      this.isShow = true;

      var kuang = document.getElementById("target");
      var x = kuang.getBoundingClientRect().left;
      var y = kuang.getBoundingClientRect().top;

      this.$nextTick(() => {
        //  var imgobj=document.getElementById("copyimg");
